@import url(https://fonts.googleapis.com/css?family=Work+Sans:400,600);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.logo-container {
  width: 90%;
}
.main-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.card {
  background: linear-gradient(-45deg, rgba(0,9,36,1) 0%, rgba(75,183,252,1) 0%, rgb(131, 82, 236) 100%);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 90%;
  padding: 0 0 1rem 0;
  min-height: 400px;
  max-width: 800px;
  border-radius: .6em;
  color: #fff;
  font-family: -apple-system,BlinkMacSystemFont,sans-serif,system-ui,"Segoe UI",Roboto,"Helvetica Neue",Arial;
  transition: all .6s cubic-bezier(0,.5,.51,1);
	box-shadow: 0px 10px 40px -5px rgba(0,0,0,0.2);
}
.steps {
  order: 0;
  margin: 2rem;
}
h1 {
  width: 100%;
  display: flex;
  justify-content: center;
  font-size: 3rem;
}

.footer {
  display: flex;
  justify-content: center;
}

.nextLabel {
  margin-top: 0.3rem;
  margin-right: 0.2rem;
}

@media only screen and (max-width: 780px) {
  .card {
    margin: 1rem;
  }
  .steps {
    margin: 1rem;
    order: 1;
  }
}

#gameArea {
  background-color: #4AA6FC;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  margin: 0 0 1rem 0;
}

.selfie {
  width: 100%;
}

.cameraButton {
  width: 100%;
  margin-top: 1rem;
  margin-bottom: 1rem;

}

#container-circles {
	position: absolute;
	left: 50%;
	bottom: 90px
}

#outer-circle {
	left: -37px;
	height: 75px;
	width: 75px;
	background-color: hsla(0, 0%, 100%, .4);
	z-index: 1
}

#inner-circle,
#outer-circle {
	position: absolute;
	border-radius: 50%
}

#inner-circle {
	left: 50%;
	top: 38px;
	height: 44px;
	width: 44px;
	background: #fff;
	margin: -22px 0 0 -22px;
	z-index: 2
}

#inner-circle.is-clicked {
	height: 38px;
	width: 38px;
	margin: -19px 0 0 -19px
}

#white-flash.normal {
	position: absolute;
	height: 100%;
	width: 100%;
	opacity: 1;
	transition: opacity .9s ease-out
}

#white-flash.do-transition {
	opacity: 0;
	background: #fff
}

#display-error {
	color: #000;
	background-color: #fff
}

.react-html5-camera-photo {
	position: relative;
	text-align: center;
}

.react-html5-camera-photo>img,
.react-html5-camera-photo>video {
	width: 80%
}

.react-html5-camera-photo>.display-error {
	width: 100%;
	margin: 0 auto;
}

@media(max-width:768px) {
	.react-html5-camera-photo>.display-error,
	.react-html5-camera-photo>img,
	.react-html5-camera-photo>video {
		width: 100%
	}
}


